<template>
    <div vu-upload class="vu-upload">
        <button class="button" @click="btnClick">+</button>
        <input type="file" ref="file" class="input" @change="change"/>

        <div class="preview" v-if="imgUrl">
            <a :href="imgUrl" target="_blank">
                <img :src="imgUrl" />
                <span class="del" @click="deleteImg">x</span>
            </a>
        </div>
    </div>
</template>

<script>
// import OSS from 'ali-oss';

/* 
https://help.aliyun.com/document_detail/32069.html#h2-url-2
https://help.aliyun.com/document_detail/64041.html
*/
const OSS_CONFIG = {
    region: 'oss-cn-hangzhou',
    accessKeyId: 'xxx',
    accessKeySecret: 'xxx',
    stsToken: '',
    bucket: 'image-xxx'
};
const IMAGE_DOMAIN = 'https://image.xxx.com';
const SIZE_UNITS = {
    'KB': 1000,
    'MB': 1000*1000,
    'GB': 1000*1000*1000
};

export default {
    props: {
        value: [String],
        width: [String],
        height: [String],
        extensions: {
            type: String,
            default: 'png,jpg,jpeg,bmp'
        },
        maxSize: [String],
        rootDirectory: {  //web, app
            type: String,
            default: 'web/'
        },
        directory: { //h5, ms, activity
            type: String,
            default: 'vui/'
        }
    },

    data(){
        return {
            sending: false,
            imgUrl: this.value
        }
    },

    watch: {
        imgUrl(newValue){
            console.log(`${newValue}`);
            this.$emit('input', newValue);
        },
        value(newValue){
            this.imgUrl = newValue;
        }
    },

    mounted(){
        // this.ossClient = new OSS(OSS_CONFIG);
        this.ossClient = {};
    },

    methods: {
        btnClick(){
            this.$refs.file.click();
        },

        change(){
            let file = this.$refs.file.files ? this.$refs.file.files[0] : {
                name: this.$refs.file.value,
                size: 0
            };
            let extensions = this.extensions,
                maxSize = this.maxSize,
                width = this.width,
                height = this.height;
            let that = this;

            //
            if(!file || !file.name){
                return;
            }

            //promis
            this.checkExtensions(file, extensions)
                .then(() => this.checkSize(file, maxSize))
                .then(() => this.checkDimension(file, width, height))
                .then(res => this.onUpload(file, res))
                .then(res => this.cbUpload(res))
                .catch(err => {
                    console.error(err)
                    that.sending = false;
                    that.$emit('error', err);
                })
        },

        //
        checkExtensions(file, extensions){
            return new Promise((resolve, reject) => {
                if(!extensions){
                    return resolve()
                }

                let fileName = file.name;
                let ext = fileName.substring(fileName.lastIndexOf('.') + 1, fileName.length).toLowerCase();
                if(typeof extensions === 'string'){
                    extensions = extensions.split(',');
                }
                if(extensions.indexOf(ext) >= 0){
                    return resolve()
                }
                return reject({
                    name: 'ExtensionError',
                    message: '' + extensions.join(', ')+ '',
                    data: {
                        extensions
                    }
                })
            })
        },

        //
        checkSize(file, maxSize){
            return new Promise((resolve, reject) => {
                if(!maxSize){
                    return resolve();
                }

                if(!isNaN(maxSize)){
                    maxSize = +maxSize;
                }else{
                    //
                    let unit = maxSize.slice(-2);
                    if(!SIZE_UNITS[unit]){
                        return reject({
                            name: 'SizeUnitError',
                            message: '',
                            data: {
                                unknownUnit: unit,
                                units: Object.keys(SIZE_UNITS)
                            }
                        });
                    }
                    maxSize = maxSize.slice(0, -2)*SIZE_UNITS[unit];
                }
                if(file.size <= maxSize){
                    return resolve();
                }

                return reject({
                    name: 'SizeError',
                    message: '',
                    data: {
                        maxSize: maxSize,
                        size: file.size
                    }
                });
            })
        },

        //
        checkDimension(file, width, height){
            return new Promise((resolve, reject) => {
                //
                let reader = new FileReader();
                reader.onload = (e) => {
                    let data = e.target.result;
                    //
                    let image = new Image();
                    image.onload = () => {
                        let imgWidth = image.width,
                            imgHeight = image.height,
                            options = {
                                width,
                                height,
                                imgWidth,
                                imgHeight
                            };

                        if(!width && !height){
                            //
                            return resolve(options)
                        }else if(width && height){
                            //
                            if((width && width == imgWidth) && (height && height == imgHeight)){
                                return resolve(options)
                            }else{
                                return reject({
                                    name: 'DimensionError',
                                    message: '',
                                    data: {
                                        ...options
                                    }
                                })
                            }
                        }else if(width){
                            //
                            if(width && width == imgWidth){
                                return resolve(options)
                            }else{
                                return reject({
                                    name: 'DimensionWidthError',
                                    message: '',
                                    data: {
                                        ...options
                                    }
                                })
                            }
                        }else if(height){
                            //
                            if(height && height == imgHeight){
                                return resolve(options)
                            }else{
                                return reject({
                                    name: 'DimensionHeightError',
                                    message: '',
                                    data: {
                                        ...options
                                    }
                                })
                            }
                        }
                    }
                    image.src = data;
                }
                reader.readAsDataURL(file);
            })
        },

        //Blob
        onUpload(file, options = {}){
            let that = this;

            //
            this.imgOptions = options;

            return new Promise((resolve, reject) => {
                //hash
                let fileName = that.hashFileName(file.name),
                    rootDirectory = that.modifyName(that.rootDirectory),
                    directory = that.modifyName(that.directory);

                that.ossClient
                    .put(`${rootDirectory}${directory}${fileName}`, file)
                    .then(res => {

                        console.log('oss:', res);
                        resolve(res);
                    })
                    .catch(err => {
                        reject({
                            name: 'UploadError',
                            message: 'oss!',
                            data: {
                                err
                            }
                        })
                    })
            })
        },

        //hash
        hashFileName(name){
            let res = name.match(/(\w*)(\.\w+)/),
                hash = Math.random().toString(32).substring(2);

            return `${res[1]}${hash}${res[2]}`;
        },

        ///
        modifyName(name){
            if(!name){
                return ''
            }
            if(name.indexOf('/') == -1){
                return `${name}/`
            }
            return name.replace(/^\//, '');
        },

        //
        cbUpload(res){
            this.sending = false;

            if(res && res.name){
                let imgUrl = `${IMAGE_DOMAIN}/${res.name}`;
                this.imgUrl = imgUrl;
                this.$emit('success', {
                    imgUrl,
                    ...this.imgOptions
                })
            }else{
                this.$emit('error', {
                    name: 'UploadError',
                    message: 'oss!'
                })
            }
        },

        deleteImg(e){
            e.preventDefault();
            this.imgUrl = '';
        }
    }
}
</script>
